<template>
	<div class="header">
		<nav class="nav">
			<div class="navbar">
				<div class="nav-container">				
					<a class="nav-brand" href="#">
						<img src="/assets/img/logo.png" alt="Platt Electric Logo" />
					</a>
				</div> 
				<div class="nav-container">
					<a v-for="(navLink, i) in navLinks"
						:key="i"
						class="nav-icon"
						href="#">
						<img :src="navLink.iconSource" />
					</a>
				</div>				
			</div>
		</nav>
	</div>
</template>

<script>
export default {
  name: "PlattHeader",
  data() {
    return {
      navLinks: [
        {
          title: "Search",
          iconSource: "/assets/icons/white/ic_search.png"
        },
        {
          title: "Cart",
          iconSource: "/assets/icons/white/ic_shopping_cart.png"
        },
        {
          title: "Account",
          iconSource: "/assets/icons/white/ic_person.png"
        },
        {
          title: "Menu",
          iconSource: "/assets/icons/white/ic_menu.png"
        }
      ]
    };
  }
};
</script>

<style lang="scss">
@import "../scss/index";
.header {
  .nav {
    background-color: $primaryBackgroundGreen;

    .navbar {			
      display: flex;
      justify-content: space-between;

      .nav-container {
				margin-top: 10px;
        width: 50%;

        &:last-of-type {
          display: flex;
					justify-content: space-between;
					
					.nav-icon {
						margin-right: 10px;
					}
        }
      }
    }
    a {
      color: #ffffff;
    }
  }
}
</style>



